@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;700&display=swap);
.card {
    padding:15px 20px;
    width:90%;
    margin: 30px auto;
    }


.models-info-container{
    display: grid;
    grid-template-columns: auto auto auto auto;    
}

.models:hover {
    cursor:pointer;
}

.models {
    height: 120px;
    border: 0.3px solid #ccc;
    margin:10px;
}
.card {
    padding:15px 20px;
    width:95%;
    margin: 30px auto;
}

.model-info-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    height:auto;

}

.form-control-label {
    cursor: pointer;
}
.form-control-label-container {
    margin: 0px !important;
}

.model-info {
    margin:20px 20px;
    width:100%
}

.image-container {
    display:flex;
}

.image-container img {
    width:80%;
    height:auto;
}


.step-info-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    height:auto;
}

.step-info-container > * {
    margin:20px 20px;
    width:100%
}

.accordion-option-groups {
    margin-bottom:20px;
    margin-top: 20px;
}

.option-group-title {
    margin-left: 15px !important;
    font-weight: 600 !important;
    text-transform: capitalize ;
}


.option-group-info-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    height:auto;
}

.option-group-form{
    margin:20px 20px;
    width:100%
}
.options-table {
    border:none;
    outline:none;
    min-width: 650px;
}

.option-modal-container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    height:auto;
    margin-top: 15px;
}


.option-img {
    width:50%;
}

.price-select:hover{
  border-color: black !important;
}

.price-select:active  {
  border-color: #3F51B5 !important;
  box-shadow: none !important;
}

.price-select {
  height:52px !important;
  border-radius: 5px !important;
}

* {
  font-family:'Roboto', Times, serif !important;
  font-weight: 300 ;
  font-size: 13px ;
}


.App {
  text-align: center;
}

.model {
  margin-left: 190px;
  margin-top: 100px; 
  font-size: 10px;
}
